<template>
  <table class="table table-bordered">
    <thead>
      <tr>
        <td v-for="g in groups" :key="g" class="text-center">
          Group {{ g }}
        </td>
      </tr>
    </thead>
    <tbody>
      <tr v-for="(row, a) in rows" :key="a">
        <td v-for="(item, b) in row" :key="b" class="text-center" :class="{'table-success': time >= item.start && time < item.end}">
          {{item.time}}
        </td>
      </tr>
    </tbody>
  </table>
</template>

<script>
import { computed } from 'vue';

export default {
  props: {
    current: Date,
    groups: Number,
    offset: Number,
    period: Number,
    start: Date,
  },
  setup(props) {
    const time = computed(() => props?.current);
    const rows = computed(() => {
      let num = props.groups * 5;
      const dates = [];
      const last = new Date(props?.start?.getTime());
      while (num--) {
        const date = {
          start: new Date(last?.getTime()),
        };
        if (props.offset) last.setHours(last.getHours() + props.offset);
        date.time = last.toLocaleTimeString('en-US', {hour: '2-digit', minute: '2-digit'});
        if (props.offset) last.setHours(last.getHours() - props.offset);
        last.setMinutes(last.getMinutes() + props.period);
        date.end = new Date(last?.getTime());
        dates.push(date);
      }
      return chunk(dates, props.groups);
    });
    return {time, rows};
  },
};


function chunk(list, len) {
  const chunked = [];
  while (len > 0 && list.length > 0) chunked.push(list.splice(0, len));
  return chunked;
}

</script>
