<template>
  <select class="form-select" v-model="offset">
    <option v-for="val in 24" :value="val - 12" :key="val">Time Zone: {{ val - 12 }}</option>
  </select>
</template>

<script>
import { computed } from 'vue';

export default {
  props: {
    modelValue: Number,
  },
  emits: ['update:modelValue'],
  setup(props, {emit}) {
    const offset = computed({
      get: () => props.modelValue,
      set: value => emit('update:modelValue', value),
    });
    return {offset};
  },
};


</script>
