<template>
  <div class="p-4">
    <Table :current="current" :groups="groups" :offset="offset-timezone" :start="startdate" :period="period" />
    <Country v-model="offset" class="mb-3" />
  </div>
</template>

<script>
import { computed, ref } from 'vue';
import Country from './components/Country.vue';
import Table from './components/Table.vue';

export default {
  components: {
    Country,
    Table,
  },
  setup() {
    const minDiff = (c, d) => (c - d)/60000;
    
    const group = ref(1);
    const groups = ref(10);
    const period = ref(30);
    const date = ref(new Date('2021-09-23T06:25:06.809Z'));
    const current = ref(new Date);
    const timezone = -current.value.getTimezoneOffset()/60;
    const offset = ref(timezone);
    const startdate = computed(() => {
      let start = new Date(date.value);
      start.setMinutes(start.getMinutes() - (group.value - 1) * period.value);
      while (minDiff(start, current.value) < -(period.value * groups.value)) {
        start.setMinutes(start.getMinutes() + period.value * groups.value);
      }
      while (minDiff(start, current.value) > 0) {
        start.setMinutes(start.getMinutes() - period.value * groups.value);
      }
      return start;
    });
    
    function updateActive() {
      current.value = new Date;
      const seconds = 60 - current.value.getSeconds() + startdate.value.getSeconds() + 1;
      setTimeout(updateActive, seconds*1000);
    }
    updateActive();
    
    fetch('https://api.jsonbin.io/b/614c0ebfaa02be1d444d2e6a/latest')
      .then(r => r.json())
      .then(({data}) => {
        date.value = new Date(data.nextGroup);
        group.value = data.inGroup;
        groups.value = data.totalGroup;
      });

      return {current, groups, offset, period, startdate, timezone};
  }
};

</script>

<style>
@import 'https://cdn.jsdelivr.net/npm/bootstrap@5.1.1/dist/css/bootstrap.min.css';
</style>